import React, { useContext, useEffect }  from 'react'
import { FormattedMessage } from 'react-intl'
import { useHistory } from 'react-router-dom'

import messages from './messages'

import Ximen from '../image/redhouse.jpg'
import Dadaocheng from '../image/tpecitygod.jpg'
import Songshan from '../image/kidmom.jpg'
import Zhongshan from '../image/eslite.jpg'
import Ximenwebp from '../image/redhouse.webp'
import Dadaochengwebp from '../image/tpecitygod.webp'
import Songshanwebp from '../image/kidmom.webp'
import Zhongshanwebp from '../image/eslite.webp'

import answersContext from '../../contexts/answers/context'

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Image from '../../components/Image'
import Container from '../../components/Container'
import Button from '../../components/Button'
import { responsive } from '../../components/ThemeProvider/theme';
import FullpageVerticalCenter from '../../components/FullpageVerticalCenter'

import Link from '../../i18n/Link';

const highest = (arr) => (arr || []).reduce(( acc, el ) => {
  acc.k[el] = acc.k[el] ? acc.k[el] + 1 : 1
  acc.max = acc.max ? acc.k[acc.max] < acc.k[el] ? el : acc.max : el
  return acc
}, { k:{} }).max

const title = ['shopping', 'shopping', 'eat', 'buy']
const location = ['Zhongshan', 'Ximen', 'Dadaocheng', 'Songshan']
const subTitle = ['Zhongshandescription', 'Ximendescription', 'Dadaochengdescription', 'Songshandescription']
const pic = [[Zhongshanwebp, Zhongshan], [Ximenwebp, Ximen], [Dadaochengwebp, Dadaocheng], [Songshanwebp, Songshan]]


const Result = ({ match: { params : { id, locale }}, ...props }) => {
  const { answers } = useContext(answersContext)
  const history = useHistory()
  useEffect(() => {
    if (!answers.length) {
      history.push(`/${locale}`)
      return
    }
    if (answers.length < 6) {
      history.push(`/${locale}/question/${answers.length + 1}`)
    }
  })
  console.log(answers, highest(answers))
  return (
    <FullpageVerticalCenter>
      <Container textAlign="center" py="1em">
        <Box my="1.375em">
          <Text.H1 as="span">
            <FormattedMessage {...messages.go} />
          </Text.H1>
          <Text.H1 as="span" mx="0.5em">
            <FormattedMessage {...messages[title[highest(answers) || 0]]} />
          </Text.H1>
          {locale === 'zh-Hant-TW' && <Text.H1 as="span">了</Text.H1>}
        </Box>
        <Text.H2 my="1.25rem"><FormattedMessage {...messages[location[highest(answers) || 0]]} /></Text.H2>
        <Text><FormattedMessage {...messages[subTitle[highest(answers) || 0]]} /></Text>
        <Box px="25%">
          <Image src={pic[highest(answers) || 1]} />
        </Box>
        <Box>
          <Button as={Link} to={`/tour/${location[highest(answers) || 0]}`} fontSize="1.25em" scale="1.25">
            <FormattedMessage {...messages.explore} />
          </Button>
        </Box>
      </Container>
    </FullpageVerticalCenter>
  )
}

export default Result
