const QuestionPage = ({ match: { params : { id, locale }}, ...props }) => {
  const { setAnswer } = useContext(answersContext)
  const history = useHistory()
  return (
    <Container py={responsive('1em', '2em')} {...props}>
      <Box position="relative" zIndex={5}>
        <Text.H2 textAlign="center">
          <FormattedMessage {...messages.whereToMorning} />
        </Text.H2>
      </Box>
      <Flex flexWrap="wrap" alignItem="center" justifyContent="center" position="relative">
        {data[id].pic.map(({ src, name }, index, { length }) => (
          <Box width={1 / 2} position="relative" zIndex={length - index} key={index}>
            <Button.Transparent
              px="0"
              py="0"
              key={name}
              onClick={() => {
                if (id < data.length - 1 ) {
                  setAnswer(id - 1, index)
                  history.push(`/${locale}/question/${1 + +id}`)
                  return
                }
                setAnswer(id - 1, index)
                history.push(`/${locale}/result`)
              }}
              scale="1.1"
            >
              <Box px="8%" >
                <Image src={src} />
              </Box>
              <Box mt="0.5em">
                <FormattedMessage {...messages[name]} />
              </Box>
            </Button.Transparent>
          </Box>
        ))}
      </Flex>
      <Flex justifyContent="center" alignItems="center" mt={responsive('6em', '8em')}>
        {range(data.length - 1).map((i) => (
          <Box
            key={i}
            position="relative"
            width={i + 1 === +id ? '1.25em' : '1em'}
            mx={responsive('1em', '1.5em')}
          >
            {i + 1 === +id && (
              <Box width={responsive('6em', '8em')} position="absolute" bottom="110%" left="-1.75em">
                <video autoPlay loop muted playsInline width="100%">
                  <source src={walkwebm} type="video/webm" />
                  <source src={walk} type="video/mp4" />
                  <img src={walkgif} alt="Loading..." />
                </video>
              </Box>
            )}
            <Circle
              border={i + 1 === +id ? '3px solid' : '2px solid'}
              borderColor={i + 1 === +id ? 'blue' : 'lightGray'}
            />
          </Box>
        ))}
      </Flex>
    </Container>
  )
}

export default QuestionPage
