import React from 'react'
import { FormattedMessage } from 'react-intl'

import messages from './messages'
import data from './data'

import Box from '../../components/Box'
import Text from '../../components/Text'
import Image from '../../components/Image'
import Container from '../../components/Container'
import Button from '../../components/Button'
import { responsive } from '../../components/ThemeProvider/theme';
import FullpageVerticalCenter from '../../components/FullpageVerticalCenter'

import Link from '../../i18n/Link';

const Tour = ({ match: { params : { location, locale }}, ...props }) => {
  const useData = data[location]
  console.log(location)
  return (
    <FullpageVerticalCenter>
      <Container textAlign="center" py="2em" {...props}>
        <Box my="1.375em">
          <Text.H1 as="span" mx="0.5em">
            <FormattedMessage {...messages[location]} />
          </Text.H1>
        </Box>
        {useData.map(({ time, src }, i) => (
          <Box key={i}>
            <Text.H2 my="1.25rem">{time}</Text.H2>
            <Text my="1em" px="2.25em" py="0.5em"><FormattedMessage {...messages[location + i]} /></Text>
            <Box px={responsive('10%', '25%')}>
              <Image src={src} />
            </Box>
          </Box>
        ))}
        <Button as={Link} to="/about" fontSize="1.25em" scale="1.25">
          <FormattedMessage {...messages.about} />
        </Button>
      </Container>
    </FullpageVerticalCenter>
  )
}

export default Tour
