import React from 'react';

import { Switch, Route } from 'react-router-dom';

import HomePage from './containers/HomePage'
import QuestionPage from './containers/QuestionPage'
import Result from './containers/Result'
import Tour from './containers/Tour'
import About from './containers/About'
import NotFoundPage from './containers/NotFoundPage'
import Layout from './containers/Layout'

import LanguageProvider from './i18n/LanguageProvider'
import Redirect from './i18n/Redirect'
import AnswersProvider from './contexts/answers/Provider'

const withI18nProvider = SubComp => props => (
  <LanguageProvider>
     <Layout>
      <SubComp {...props} />
    </Layout>
  </LanguageProvider>
)

const paths = [
  { path: '/', component: HomePage, exact: true },
  { path: '/about', component: About, exact: true },
  { path: '/question/:id', component: QuestionPage, exact: true },
  { path: '/result', component: Result, exact: true },
  { path: '/tour/:location', component: Tour, exact: true },
]

function App() {
  return (
    <AnswersProvider>
      <Switch>
        {paths.map((p, i) => (
          <Route
            key={`redirect-${i}`}
            exact
            path={p.path}
            component={withI18nProvider(Redirect)}
          />
        ))}
        {paths.map((p, i) => (
          <Route
            key={`locale-${i}`}
            path={`/:locale${p.path}`}
            component={withI18nProvider(p.component)}
            exact={p.exact}
          />
        ))}
        <Route path="" component={NotFoundPage} />
      </Switch>
    </AnswersProvider>
  );
}

export default App;
