import React from 'react'
import { FormattedMessage } from 'react-intl'

import Container from '../../components/Container'
import Box from '../../components/Box'
import Text from '../../components/Text'
import Image from '../../components/Image'
// import BackgroundImage from '../../components/BackgroundImage'
import Button from '../../components/Button'
import FullpageVerticalCenter from '../../components/FullpageVerticalCenter'
import { responsive } from '../../components/ThemeProvider/theme';

import Link from '../../i18n/Link'

import messages from './messages'
import Language from '../Language'

const buttonData = [
  {
    text: 'contact',
    link: '/'
  },
  {
    text: 'share',
    link: '/'
  },
  {
    text: 'introduce',
    link: '/'
  },
]

const HomePage = ({  match: { params : { id, locale }}, ...props }) => {
  return (
    <FullpageVerticalCenter>
      <Container textAlign="center" {...props}>
        <Text.H2 my="1em" color="primary">
          <FormattedMessage {...messages.title} />
        </Text.H2>
        <FormattedMessage {...messages.description} />
        <Box px={responsive('5%', '10%')} mx="auto">
          <Image src={[]} />
        </Box>
        {buttonData.map(({ text, link }, k) => (
          <Box mt="0.5em" key={k}>
            <Button as={Link} to={link} fontSize="1.25em" scale="1.25">
              <FormattedMessage {...messages[text]} />
            </Button>
          </Box>
        ))}
        <Box mt="2em" mb="1em">
          <Language to />
        </Box>
      </Container>
    </FullpageVerticalCenter>
  )
}

export default HomePage
