import React from 'react'
import { FormattedMessage } from 'react-intl'

import Container from '../../components/Container'
import Box from '../../components/Box'
import Text from '../../components/Text'
import Image from '../../components/Image'
// import BackgroundImage from '../../components/BackgroundImage'
import Button from '../../components/Button'
import FullpageVerticalCenter from '../../components/FullpageVerticalCenter'
import { responsive } from '../../components/ThemeProvider/theme';
import useResponsive from '../../contexts/mediaQuery/useResponsive'

import Link from '../../i18n/Link'

import messages from './messages'
import walk from './walk.mp4';
import walkgif from './walk.gif';
import walkwebm from './walk.webm';
import Language from '../Language'

const HomePage = (...props) => {
  // const { isMobile } = useResponsive()
  return (
    <FullpageVerticalCenter>
      <Container textAlign="center" {...props}>
        <Text.H2 color="primary">
          <FormattedMessage {...messages.whereGo} />
        </Text.H2>
        <FormattedMessage {...messages.description} />
        <Box px={responsive('5%', '10%')} mx="auto">
          <video autoPlay loop muted playsInline width="100%">
            <source src={walkwebm} type="video/webm" />
            <source src={walk} type="video/mp4" />
            <img src={walkgif} alt="Loading..." />
          </video>
        </Box>
        <Button as={Link} to={'/question/1'} fontSize="1.25em" scale="1.25">ＧＯ！</Button>
        <Box mt="2em" mb="1em">
          <Language />
        </Box>
        {/* <Flex mt="1.5em" justifyContent="center" alignItems="center">
          <Text>Made by</Text>
          <Box as={Link} href="https://relab.cc/">
            <Image width="auto" height="2em" src={relab} />
          </Box>
        </Flex> */}
      </Container>
    </FullpageVerticalCenter>
  )
}

export default HomePage
